.itinerary{
	background:white;
	min-height:500px;
	height:auto;
	padding-right:0;
	padding-left:0;
	margin-top:8rem;
	overflow-x:hidden;
}
.itinerary .itineraryheader{
	height:50px;
	margin-top:20px;
	display:flex;
	flex-direction: column;
}
.itinerary .itineraryheader h2{
	text-transform:uppercase;
	text-align:center;
	font-family:"Times new roman";
	opacity:0.9;
}
.itinerary .itineraryheader small{
	text-align:center;
	opacity:0.9;
	font-style:italic;
}
.itinerary .carditineraryholder{
margin-top:6rem;
min-height:300px;
height:auto;
margin-bottom:2rem;
display:flex;
 transition: .3s ease-in;

}
.itinerary .carditineraryholder .forcardsitinerary{
	padding:10px;
	min-width:100%;
	min-height:400px;
    margin:0px auto;
    border:1px solid white;

    
}
.itinerary .carditineraryholder .forcardsitinerary{
	display:flex;
	justify-content:space-around;
	box-shadow:0px 0px 0px 0px rgba(0,0,0,0.19);

}

.itinerary .carditineraryholder .forcardsitinerary div{
	min-width:300px;
	height:400px;
	box-shadow:0px 8px 6px 0px rgba(0,0,0,0.19);
	background:#F5F5F5;
	cursor:pointer;
	border-radius:10px;
	display:grid;
	grid-template-rows: 50% 49%;
	grid-column-gap:5px;
	border:1px solid #F5F5F5;
	transform:scale(0.9);
	transition: transform .5s ease-in-out;

}
.itinerary .carditineraryholder .forcardsitinerary .holderofbookbutton{
	background:#22273B;
	width:100%;
	height:400px;
	z-index:1000;
	box-sizing:border-box;
	padding-left:0;
	padding-right:0;
	position:absolute;
    display:none;
    opacity:0.9;
    color:white;
    display:flex;
    visibility:hidden;
    justify-content:center;
    align-items:center;

}
.itinerary .carditineraryholder .forcardsitinerary .holderofbookbutton button{

	   width:200px;
	   padding:10px;
	   border:1px solid transparent;
	   background:#2EA19A;
	   border-radius:23px;
	   display:flex;
	   justify-content:space-around;
	   color:white;
	   box-shadow:0px 6px 16px 0px rgba(0,0,0,0.19);
	   outline:none;

}
.itinerary .carditineraryholder .forcardsitinerary .holderofbookbutton button span{
	font-size:18px;
}
.itinerary .carditineraryholder .forcardsitinerary div:hover > .holderofbookbutton {
	transform:scale(1.1);
	visibility:visible;
}

.itinerary .carditineraryholder .forcardsitinerary .card .forimage{
  border-radius:8px 8px 0px 0px;
  height:200px;
}
.itinerary .carditineraryholder .forcardsitinerary .card .forimage img{
	height:200px;
	width:320px;
	padding-left:0;
	padding-right: 0;
	border-radius:8px 8px 0px 0px;
}

.itinerary .carditineraryholder .forcardsitinerary .card .fortexts{
 padding:10px;
 display:flex;
 flex-direction:column;
 justify-content:space-between;

}
.itinerary .carditineraryholder .forcardsitinerary .card .fortexts #forpriceholderandtime{
	display:flex;
	justify-content:space-between;
}
.itinerary  .forcontrolsbutton{
	padding:10px;
	width:80%;
	margin:0px auto;
	display:flex;
	justify-content:space-between;
}
.itinerary  .forcontrolsbutton button{
	width:50px;
	height:50px;
	border-radius:50%;
	border:1px solid transparent;
	background:#089284;
	box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
	color:white;
	outline:none;
	font-size:27px;
	display:flex;
	align-items:center;
	justify-content: center;

}

@media (max-width:900px)
{
	#rowcardthree{
		display:none
	}
}
@media (max-width:600px){
	#rowcardthree,#rowcardtwo{
		display:none
	}
	.itinerary .carditineraryholder .forcardsitinerary div{
		min-width:322px;
		margin-left:15px;
	}
	.itinerary{
		margin-top:20rem;
	}
}