.forabout{
	min-height:300px;
	padding-left:0;
	padding-right:0;
	height:auto;
	margin-bottom:1rem;
	padding:40px;
}
.forabouttitle{
	height:50px;
	padding:10px;
	display:flex;
	justify-content:center;
	align-items:center;
}
.forabouttitle h2{
	text-transform:uppercase;
	text-align:center;
	opacity:0.9;
	font-size:26px;
	font-family:"Times New Roman"
}
.foraboutcontent{
	height:220px;
	background:red;
	box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
	margin-bottom:100px;
	width:80%;
	margin:-14rem auto;
}
.aboutimageholder
{
	 margin-left:0;
 margin-right:0;
 padding-right:0;
 padding-left:0;
}
.aboutimageholder img{
  width:100%;
  height:220px;

}
.forabouttexts{
	background:white;
	padding:10px;
	display:flex;
	flex-direction:column;
}
.forabouttexts h4{
	color:#7FC51C;
	margin-top:10px;
	text-align:center;

}
.forabouttexts h6{
	line-height:2rem;
	letter-spacing:2px;
	margin-top:10px;
}