.Traveldetail{
	background:#F5F5F5;
	min-height:650px;
	height:auto;
	margin-bottom:10px;
	padding:5px;
	margin-top:1.8rem;
	overflow-x:hidden;
}
.traveldetailheader{
	background:transparent;
	height:320px;
	box-shadow:0px 4px 10px 0px rgba(0,0,0,0.19);
	background-image:url("../assets/rwanda.jpg");
}
.traveldetailheaderimage{
	background-image:url("../assets/travelbg.png");
	background-size:cover;
	height:320px;
	display:flex;
	justify-content:center;
}
.traveldetailheaderimage img{
	height:318px;

}
.traveldetailheaderimage h2{
	position:absolute;
	display:flex;
	justify-content:center;
	align-items:center;
	text-transform:uppercase;
	width:400px;
	margin-left:19rem;
	margin-top:2rem;
	word-spacing:18px;
	font-family:"sans selif";
	font-size:40px
}
.traveldetailheaderimage h2 span{
	margin-left:-2rem;
	color:white;
}
.traveldetailheaderm{
 min-height:100px;
 background:#F5F5F5;
 margin-top:1.5rem;
 padding:5px;
 height:auto;
box-shadow:0px 0px 0px 0px rgba(0,0,0,0.19);
box-sizing: border-box;

}
.travelholderoficon{

	 display:flex;
 justify-content:center;
 align-items:center;

}
.travelholderoficon div{
	background:white;
	width:120px;
	height:140px;
	box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
	border-radius:5px;
	margin-left:14rem;	
		 display:flex;
 justify-content:center;
 align-items:center;
}
.travelholderoficon h2{
	margin-left:5rem;
	font-family:"sans selif";
	font-size:18px;
	background:red;
}
.travelholderoftitle{
 display:flex;
 justify-content:flex-start;
 text-align:left;
 align-items:center;
}

.forimagetravel{
	height:600px;
	padding:5px;
	margin-top:10px;


}
.textonimage{
	position:absolute;
	width:90%;
	height:250px;
	margin-top:5rem;
	display:flex;
	align-items:center;
	justify-content:center;
	
}
.textonimage h4{
  color:white;
  font-size:35px;
  font-family:"Sans selif"	
}
.imagetravel{
	height:590px;
}
.imagetravel img{
	width:100%;
	height:590px;
}
@media (max-width:1024px){



	.traveldetailheaderimage img{
		margin-left:-4rem;
	}
}
@media (max-width:900px){

	.traveldetailheaderimage{
		justify-content:flex-start;
	}
	.traveldetailheaderimage img{
		margin-left:4rem;
	}
	.traveldetailheaderimage h2 span{
		color:black;
	}
	.travelholderoficon div{
		margin-left:1rem
	}
	.travelholderoftitle{
		text-align:center;
		justify-content:center;
		margin-top:15px;
	}
	.textonimage h4{
		font-size:20px;
	}
}
@media (max-width:600px){

	.traveldetailheaderimage{
		justify-content:flex-start;
	}
	.traveldetailheaderimage img{
		margin-left:2px;
	}
	.traveldetailheaderimage h2{

		justify-content:flex-start;
		margin-left:14rem;
		margin-top:5rem;
		width:280px;
	}
	.traveldetailheaderimage h2 span{
		color:black;
	}
	.travelholderoficon div{
		margin-left:-8rem;
	}
	.travelholderoftitle{
		text-align:center;
		justify-content:center;
		margin-top:15px;
		text-align:center;
	}
	.travelholderoftitle h2{
		margin-left:-5rem;
	}
}
@media (max-width:600px){

	.traveldetailheaderimage h2{

		justify-content:flex-start;
		margin-left:10rem;
		margin-top:1rem;
		width:280px;
		font-size:25px;
	}
	.travelholderoftitle{
		justify-content:center;
	}
	.travelholderoftitle h2{
		margin-left:10px;
	}
	.travelholderoficon div{
		margin-left:2rem;
	}
	}