.fornav{
width:100%;
height:50px;
box-shadow:0px 0px 20px 0px rgba(0,0,0,0.19);
position:fixed;
top:0;
background-color:white;
display:flex;
justify-content:space-around;
align-items:center;
z-index:100;
}
.forlogo{
	background:red;
}
.forlogoimg{
  width:100px;
  height:30px;
}
.formenus{
	font-size:18px;
	font-family:"Times New Romans";
	height:49px;
	width:50%;

}
.formenus ul{
	display:flex;
	justify-content:space-between;
	list-style:none;
}
.signupb{
	background-color:red; 
	outline:none;
}
.link{
	text-decoration:none;
	color:black;
	text-transform:uppercase;
	font-size:12px;
	margin-top:10px;
}
.link:hover,.link:active{
	color:#7CC01B;
}
.linkactive{
	color:#7CC01B;
}
/*togglebutton styles*/
.buttontoggle{
 background:white;
 color:black;
 border:1px solid white;
 box-shadow:0px 0px 2px 0px rgba(0,0,0,0.19);
 outline:none;
 float:right;
}
.buttontoggle:hover{
	cursor:pointer;
	outline:none;
}
/*div hold menu for responsive styles*/
.forresponsivemenu{
width:0%;
height:100%;
background:white;
box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
z-index:1500;
position:absolute;
position:fixed;
left:0;
top:0;
overflow-x:hidden;
transition:width .2s ease-in-out;
display:flex;
flex-direction:column;
justify-content:flex-start;
}
.forresponsivemenu.animatewidth{
	width:80%;
}
.forbackresponsive{
width:0%;
height:100%;
background:black;
opacity:0.7;
box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
position:absolute;
position:fixed;
z-index:200;
top:0;
left:0;	
}
.forbackresponsive.fullwidth{
	width:100%;
}
.forresponsivemenu .resforlog{
	margin-top:15px;
}
.forresponsivemenu .resformenus{
	margin-top:10px;
	min-height:500px;
	height:auto;
	width:100%;
}

.resformenus ul{
	display:flex;
	flex-direction:column;
	justify-content:flex-start;
	list-style:none;

	align-items:center;
}
.resformenus ul li{
	margin-top:50px;
	margin-left:-50px;
}
.ressignupb{
	background-color:red; 
}
.reslink{
	text-decoration:none;
	color:black;
	margin-left:-10px;
}
.reslink:hover,.link:active{
	color:#ff6000;
}
@media (min-width:901px)
{
	.buttontoggle{
		display:none;
	}
}
@media (max-width:900px)
{
	.formenus ul{
		display:none;
	}
}

