.subscribe{
	background:#22273B;
	min-height:380px;
	height:auto;
	padding:8px;
}
.subscribeholder{
	background:#22273B;
	min-height:390px;
	height:auto;
	color:white;

}
.forsubscribetitle {
	border-right:0px solid grey;
	display:flex;
	flex-direction:column;
	justify-content:flex-start;	
	align-items:center;
}
.forsubscribetitle h3{
	text-transform:uppercase;
	margin-top:6rem;
}
.forsubscribetitle h3::before{
	position:absolute;
	content:"";
	width:200px;
	border:2px solid grey;
	margin-top:45px;
	margin-left:60px;
	background:#93C449;
	border-color:#93C449;
}
.forsubscribetitle small{
	font-size:12px;
	margin-top:20px
}
.subscribeform {
	display:flex;
	justify-content:center;
	align-items:center;
	flex-direction:column;

}
.subscribeform input{
		margin-top:20px;
		width:70%;
		padding:10px;
		border:1px solid white;
		border-radius:10px;
		height:60px;

}
.subscribeform input::placeholder{
	color:#000000;
	text-align:center;
}
.subscribeform button{
	background:#089284;
	padding:10px;
	border:1px solid transparent;
	color:white;
	outline:none;
	width:150px;
	font-size:15px;
	box-shadow:0px 7px 0px 0px rgba(0,0,0,0.19);
}