.Travel{
	background:white;
	min-height:350px;
	height:auto;
	margin-bottom:10px
}
.traveltitle{
	height:50px;
	background:#f5f5f5;
}
.traveltitle h2{
	text-align:center;
	text-transform:uppercase;
	opacity:0.8;
}
.forimagetravel{
	min-height:250px;
	padding:10px;
	height:auto;

}
.travellink{
	text-decoration:none;
	border-bottom:0px solid grey;
}
.travellink:hover{
text-decoration:none;
border-bottom:0px solid grey;
}
.TravelImage{
	background:white;
	height:270px;
	margin:15px auto;
	box-sizing:border-box;
}
.TravelImage img{
	width:100%;
	height:270px;
	margin:0px auto
}
.forbuttondetail{
	height:80px;
	display:flex;
	justify-content:center;
	align-items: center;
	text-align:center;
}
.buttondetailtravel{
	display:flex;
	justify-content:center;

}
.buttondetailtravel button{
	width:210px;
	padding:10px;
	border-radius:23px;
	border:1px solid transparent;
	display:flex;
  justify-content:space-around;
  background:#089284;
  color:white;
  box-shadow:0px 6px 6px 0px rgba(0,0,0,0.19);
  margin-bottom:10px;
  outline:none;
}
.buttondetailtravel button span{
	font-size:20px;
}