
.headerslider{
	padding-right:0;
	padding-left:0;
	min-height:400px;
	box-shadow: 0px 1px 0px 0px rgba(0,0,0,0.19);
	margin-top:30px;
	overflow-x:hidden;
}
.headerslider .headerslidercol{
	display:flex;
	padding-right:0;
	padding-left:0;
	margin-right:0;
	margin-left:0;
	transition: .2s ease-in-out;
}
.headerslider .headerslidercol .sectionforimageslider{
min-width:100%;
height:400px;
display:flex;
transition: .3s ease-in-out;
}
.headerslider .headerslidercol .sectionforimageslider img{
	min-width:100%;
	height:400px;
}
.headerslider .headerslidercol .forpreandnextbutton{
    width:100%;
    height:40px;
    align-items:center;
    display:flex;
    margin-top:8rem;
    position:absolute;
    float:left;
    padding:10px;
    justify-content:space-between;
    z-index:1000;
} 
.headerslider .headerslidercol .idprevbutton{
	background-color:white;
    width:50px;
    height:40px;
    border:1px solid white;
    opacity:0.8;
    box-sizing:border-box;
    outline:none;
}
.headerslider .headerslidercol .idnextbutton{
	background-color:white;
    width:50px;
    height:40px;
    border:1px solid white;
    opacity:0.8;
    box-sizing:border-box;
     outline:none;
}
.forchekinandoutholder{
	background:white;
	width:80%;
	height:100px;
	margin:-50px auto;
	transform:translate(12%,0%);
	position:absolute;
	box-shadow:2px 6px 20px 0px rgba(0,0,0,0.19);
	box-sizing:border-box;
	display:grid;
	padding-left:0;
	padding-right:0;
	grid-template-columns:30% 30% 40%;
}
 #forbookbutton{
	background:#13A299;
	height:100px;
	width:100%;
}
#forarrive,#fordeparture{
 
 display:flex;
 flex-direction:column;
 padding:10px;
 align-items:center;
 font-size:18px;
 text-align:center;
 justify-content:space-around;
 font-family:"Times New Romans"
}
#forarrive input,#fordeparture input{
background:white;
box-shadow:0px 0px 10px 0px rgba(0,0,0,0.19);
border:none;
padding:6px;
width:80%;
border-radius:5px;
}
#forbookbutton{
	display:flex;
	align-items:center;
	justify-content:center;
}
#forbookbutton button{
	background-color:#2EA19A;
	width:200px;
	padding:10px;
	box-shadow:0px 5px 15px 0px rgba(0,0,0,0.19);
	border:1px solid transparent;
	color:white;
	text-transform:uppercase;
	letter-spacing:4px;
	border-radius:23px;

}
@media (max-width:500px)
{
	.forchekinandoutholder{
	width:100%;
	height:290px;
	display:flex;
	flex-direction:column;
	margin-left:-3.5rem;
	margin-bottom:4px;
}
#forbookbutton{
	height:200px;
}
}
@media (max-width:600px)
{
	.forchekinandoutholder{
	width:80%;
	height:290px;
	display:flex;
	flex-direction:column;
	margin-left:0rem;
	margin-bottom:4px;
}
#forbookbutton{
	height:200px;
}
}
